body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  text-align: center;
  background-color: #333;
  padding: 20px;
}

.search-form {
  text-align: center;
  margin: 20px auto;
}

.search-form input[type="text"] {
  width: 300px;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #333;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 20px;
  display: flex;
}

.photo-card {
  width: 300px;
  height: 400px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;
  display: flex;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
}

.info {
  flex-grow: 0;
  justify-content: space-around;
  padding: 10px;
  display: flex;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #333;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  display: none;
}

.message {
  text-align: center;
  margin-top: 20px;
}

.info-item {
  flex-direction: column;
  align-items: center;
  font-size: smaller;
  display: flex;
}

.info-item:not(:first-child) {
  margin-left: 5px;
}

